.content {
  margin-left: 2rem;
  margin-right: 2rem;
  margin-bottom: 2rem;
}

.app-header {
  box-shadow: 0 4px 6px -2px rgba(221,221,221,0.64);
}

header {
  margin-top: 0.5rem;
}

header ul {
  margin-left: 2rem;
  margin-right: 2rem;
}

.nav {
  padding-bottom: 0.1rem;
}

.nav-ul {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin-top: 0;
  margin-bottom: 1rem;
  padding-left: 0;
  list-style: none;
}

.nav .username {
  margin-right: 1rem;
}

.nav .search-input {
  margin-right: 1rem;
}

.nav .search {
  color: black;
  font-weight: 600;
  font-size: 1.2rem;
}

.nav .add-new {
  color:  black;
  font-weight: 600;
  font-size: 1.2rem;
}

.nav .logout {
  color:  black;
  font-weight: 600;
  font-size: 1.2rem;
}

.nav .logout button {
  color:  black;
  font-weight: 600;
  font-size: 1rem;
}

.nav .login {
  color:  black;
  font-weight: 600;
  font-size: 1.2rem;
}

.logo {
  font-family: Comic Sans MS, Comic Sans, cursive;
  font-size: 3rem;
  font-style: italic;
  color: #0abab5;
  font-weight: 600;
}

.logo-li {
}

.nav li {
  list-style: none;
  align-self: center;
}

.img-preview {
  width: 17rem;
  height: 17rem;
  border-radius: 4px;
}

a {
  text-decoration: none;
  color: black;
}

.home {
  display: grid;
  grid-template-columns: 4fr 1fr;
}

.home .home-ul {
  display: flex;
  flex-flow: row wrap;
  list-style: none;
  padding-left: 0;
  font-weight: 600;
}

.home .home-ul li {
  width: 17rem;
  margin-right: 1rem;
}

.my-events {
  width: 10rem;
}

.my-events .my-ul {
  list-style: none;
  padding-left: 0;
  font-weight: 600;
}

.my-events img {
  width: 10rem;
  height: 10rem;
  border-radius: 4px;
}

.add-page {
  margin-left: auto;
  margin-right: auto;
}

.login-page {
  display: flex;
  justify-content: center;
  margin-top: 12rem;
}

.login-page .login-second {
  display: flex;
  flex-flow: column wrap;
  width: 20rem;
  font-size: 1.2rem;
}

.login-page .login-input {
  display: flex;
  flex-flow: column wrap;
}

/* .name-input {
  width: auto;
}
*/


.password-input {
  margin-bottom: 1rem;
} 

.login-page button {
  font-size: 1.2rem;
}

.event-page {
  display: grid;
  grid-template-columns: 4fr 1fr;
}

.event-page img {
  width: 30rem;
  height: 30rem;
  border-radius: 4px;
}

.event-page .event-side {
  margin-top: 5rem;
}

.event-page .event-side ul {
  list-style: none;
  padding-left: 1rem;
  margin-top: 0.4rem;
}

.event-page .event-side .participants {
  font-weight: 600;
}

.event-page .event-side .sign-up {
  margin-bottom: 1rem;
}

.event-page .event-side button {
  font-size: 1.2rem;
}

.event-page .event-side button:hover {
  background-color: #0abab5;
  color: white;
  border: #0abab5;
}

.add-page {
  display: flex;
  flex-flow: column wrap;
  width: 60%;
}

.add-page textarea {
  height: 20rem;
}

.add-page button {
  margin-top: 1rem;
  font-size: 1.2rem;
}

.add-page button:hover {
  background-color: #0abab5;
  color: white;
  border: #0abab5;
}